import React, { useState} from 'react';
import s from './Profile.module.css';
import Preloader from '../../common/preloader/preloader';
import userPhoto from '../../../assets/images/1.jpg';
import ProfileData from './ProfileData';
import ProfileDataForm from './ProfileDataForm';
import ProfileStatusHooks from './ProfileStatusHooks';

const Profile = (props) => {

    let [editMode, setEditMode] = useState(false);
    if (!props.profile) {
        return <Preloader />;
    }
    const onLogoPhotoSelected = (e) => {

        if (e.target.files.length) {
            console.log(e.target.files[0])
            props.savePhoto(e.target.files[0]);
        }
    }
    return <div className={s.content}>
        <div className={s.leftBlock}>
            <div className={s.containerLogo}><img src={props.profile.photos.small != null ? props.profile.photos.large : userPhoto} className={s.bigLogo} alt="User Large" />
                {props.isOwner && <div>
                    <label for={"imageUpload"} className={s.btn} >Edit</label>
                    <input type={"file"} id={"imageUpload"} className={s.input} onChange={onLogoPhotoSelected} accept=".png, .jpg, .jpeg" /></div>}</div>
        <ProfileStatusHooks status={props.status} updateStatus={props.updateStatus} />
        <div>
            {props.followStatus
                ? <button disabled={props.followInProgres.some(id => id === props.profile.userId)}
                    onClick={() => { props.unfollow(props.profile.userId) }}>Unfollow</button>
                : <button disabled={props.followInProgres.some(id => id === props.profile.userId)}
                    onClick={() => { props.follow(props.profile.userId) }}>Follow</button>}
            </div>
        </div>
        <div className={s.rightBlock}>
        {editMode
            ? <ProfileDataForm props={props} disableEditMode={()=>{setEditMode(false)}}/>
            : <ProfileData props={props} goToEditMode={()=>{setEditMode(true)}} />
            }
        </div>
</div>
};

export default Profile;