import React from "react";
import { Field } from "formik";
import s from './Profile.module.css';


export const Contacts = ({ contactTitle, contactValue }) => {
    if (contactValue) {
        const activeContacts = `This is my ${contactTitle}`
        return <div><b>{contactTitle}: </b><a href={`https://${contactValue}`} className={`${s.contactsLink} ${s.linkHover}`}>{activeContacts}</a></div>
    }
    return <div><b>{contactTitle}: </b><a href={`https://${contactValue}`} className={`${s.contactsLink} ${s.linkHover}`}></a></div>
}


export const ContactsForm = ({contactTitle, contactValue}) => {
    return (<div><div><b>{contactTitle}: </b></div><div><Field
                        name={`contacts.${contactTitle}`}
                        type={'text'}
        placeholder={contactValue} /></div>
    </div>
    )
}
