import React from 'react';
import s from './Post.module.css'

class Post extends React.Component {

    render() {
        return (
            <div className={s.content}>
                {this.props.dataBase.map(el => 
                    <div className={s.massage}>
                        <img src={el.imgLogo} className={s.LogoImage} alt={el.name} />
                        <div className={s.PostName}>{el.name}</div>
                        <div className={s.PostText}>{el.post}</div>
                    </div>
                )}
            </div>
        );
    }
}
export default Post;