import { createSelector } from 'reselect';

const GetUsersForSelect = (state) => {
    return state.usersPage.users;
}

export const selectGetUsers = createSelector(GetUsersForSelect, (users) => {
    return users.filter(u => true)
})

export const selectPageSize = (state) => {
    return state.usersPage.pageSize;
}
export const selectTotalUsersCount = (state) => {
    return state.usersPage.totalUsersCount;
}
export const selectCurrentPage = (state) => {
    return state.usersPage.currentPage;
}
export const selectIsFetching = (state) => {
    return state.usersPage.isFetching;
}
export const selectfollowInProgres = (state) => {
    return state.usersPage.followInProgres;
}