import React from 'react';
import s from './Content.module.css';
import Post from './Post/Post.jsx';
import header from '../../assets/images/header.jpg';
import Logo from '../../assets/images/logo.jpg';
import { Link } from "react-router-dom";


const Content = (props) => {
    return <div className={s.content}>
        <img src={header} className={s.imageHeader} alt="CatSocial network"/>
        <div >
            <Link to={`/profile/` + 23626} className={`${s.mainLink} ${s.linkHover}`}>
                <div className={s.myLink}>
                <div className={s.text}>Please, view my profile here</div>
                    <div><img src={Logo} className={s.LogoImage} alt="User Logo" /></div>
                </div>
          </Link>
        </div>
        <Post dataBase={props.dataBase} />
    </div>
};
export default Content;