import React from "react";
import { Formik, Form, Field, ErrorMessage } from "formik";
import * as Yup from "yup";
import s from './Profile.module.css';
import catWork from '../../../assets/images/cat-work.jpg';
import catLookingJob from '../../../assets/images/cat-looking-job.jpg';
import { ContactsForm } from "./Contacts";

const validationSchemaProfileForm = Yup.object().shape({
   fullName: Yup.string()
      .min( 2, "Must be longer than 2 characters" )
      .max( 20, "Must be shorter than 20 characters" )
        .required("Name is required"),
    aboutMe: Yup.string()
      .min( 2, "Must be longer than 2 characters" )
      .max( 300, "Must be shorter than 300 characters" )
        .required("About Me is required"),
    lookingForAJobDescription: Yup.string()
      .min( 2, "Must be longer than 2 characters" )
      .max( 300, "Must be shorter than 300 characters" )
        .required("My proffesional skills is required"),

} );

const ProfileDataForm = (props) => {
    return (<div>
         <Formik
            initialValues={{
                fullName: props.props.profile.fullName,
                aboutMe: props.props.profile.aboutMe,
                lookingForAJob: false,
                lookingForAJobDescription: props.props.profile.lookingForAJobDescription,
                contacts: {
                    facebook: props.props.profile.contacts.facebook,
                    website: props.props.profile.contacts.website,
                    twitter: props.props.profile.contacts.twitter,
                    instagram: props.props.profile.contacts.instagram,
                    youtube: props.props.profile.contacts.youtube,
                    github: props.props.profile.contacts.github,
                    mainLink: props.props.profile.contacts.mainLink,
                }    
            }}
            validationSchema={validationSchemaProfileForm}
            onSubmit={(values) => {
                props.props.saveProfile(values);
                props.disableEditMode();
            }}>
            {() => (
                <Form>
                    <button type={'submit'}>Save</button>
                    <ErrorMessage name="facebook" component="div" className={s.error}/>
                    <div className={s.userId}><b>User ID: </b>{props.props.profile.userId}</div>
                    <div className={s.about}><b>My Name: </b></div>
                    <div><Field
                        name={'fullName'}
                        type={'text'}
                        placeholder={'My name'} /></div>
                    <ErrorMessage name="fullName" component="div" className={s.error}/>
                    <div>
                         <div className={s.about}><b>About me: </b></div><div><Field
                        name={'aboutMe'}
                        type={'text'}
                        placeholder={'aboutMe'} /></div>
                        <div></div>
                        <ErrorMessage name="aboutMe" component="div" className={s.error}/>
                    <div>
                            <b>Loking for a job: </b>
                            <div role="group" aria-labelledby="my-radio-group">
                                <label>
                                <Field type="radio" name="lookingForAJob" value="true" />
                                Cat Worked
                                </label>
                            </div>
                            <div>{props.props.profile.lookingForAJob
                                ? <img src={catWork} alt="cat worked" className={s.catWork} />
                                : <img src={catLookingJob} alt="cat looking for a job" className={s.catWork} />}</div>
                    </div>
                        <div>
                            <b>My proffesional skills: </b></div><div><Field
                        name={'lookingForAJobDescription'}
                        type={'text'}
                        placeholder={'mySkills'} />
                        </div>
                    </div>
                    <ErrorMessage name="lookingForAJobDescription" component="div" className={s.error}/>
                    <div><b>Contacts:</b></div>
                    <div>{Object.keys(props.props.profile.contacts).map(key => {
                        return <div><ContactsForm contactTitle={key} contactValue={props.props.profile.contacts[key]}/>
                            <ErrorMessage name={key} component="div" className={s.error}/>
                        </div>
                    })}</div>
                    
                </Form>)}
    </Formik></div>
    )
}

export default ProfileDataForm;